.pie-div {
  display: flex;
  width: 100%;
  align-items: center;
  flex-direction: row;
  justify-content: center;
  /* background-color: aquamarine; */
}

.pie-chart-container {
  width: 30%;
  display: flex;
  justify-content: right;
}

.pie-chart {
  width: 10vw; /* Adjust width as needed */
  height: 10vw; /* Adjust height as needed */
}

.pie-chart-segment {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  position: relative;
}

.white-circle {
  width: 80%; /* Adjust size as needed */
  height: 80%; /* Adjust size as needed */
  border-radius: 50%;
  background-color: var(--thrid-color);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.pie-key {
  /* background-color: blue; */
  text-align: left;
  display: flex;
  flex-direction: column;
  align-items: baseline;
  justify-content: baseline;
  width: 30%;
}
